@import url("./reset.css");

@import url("https://fonts.cdnfonts.com/css/sf-pro-display");
body {
    font-family: "SF Pro Display", sans-serif;
    font-size: 1em;
}
.flex_container {
  display: flex;
  width: 100%;
}
.main {
  display: flex;
  width: 100%;
  flex-flow: column nowrap;
}
.navbar_main {
  width: 100%;
  height: 100vh;
  max-width: 240px;
  background: #091336;
  grid-area: navbar;
}


/* Header */
.header {
    display: flex;
    max-width: 1680px;
    max-height: 65px;
    height: 100%;
    width: 100%;
    padding: 0 120px;
    align-items: center;
    justify-content: space-between;
    background: #FFF;
    box-shadow: 0px 4px 5px #E9EDF3;
}
.header__date {
    color: #899CB1;
    font-size: 0.9rem;
}
.header__date::first-letter {
    text-transform: capitalize;
}
.red {
    color: #EA1A4F;
}
.yellow {
    color: #FFB800;
}
.green {
    color: #00A775;
}
.header__analitic_wrapper {
    display: flex;
    gap: 0 56px;
    font-size: 0.8rem;
}
.calls_message {
    padding: 0 0 7px 0;
}
.progress_bar {
    position: relative;
    height: 6px;
    width: 100%;
    max-width: 156px;
    background:  #DEE6F5;
    border-radius: 12px;
    transition: all .5s ease-in-out;
}
.header__profile_search {
    width: 16px;
    height: 16px;
    background: url('../assets/icons/search.png') no-repeat;
    cursor: pointer;
}
.header__profile_avatar {
    display: flex;
    align-items: center;
    gap: 0 5px;
    cursor: pointer;
}
.header__profile {
    display: flex;
    gap: 0 65px;
    align-items: center;
}
/* Navbar */
.navbar__logo {
  width: 100%;
  padding: 20px 12px;
}
.navbar__list_item {
  display: flex;
  gap: 0 14px;
  align-items: center;
  color: #fff;
  padding: 0 12px;
  width: 100%;
  height: 52px;
}
.navbar__list_item:hover,.navbar__list_item:active, .item_active {
  background: #d8e4fb52;
  cursor: pointer;
}
.navbar__buttons_wrapper {
  display: flex;
  flex-flow: column nowrap;
  gap: 30px;
  padding: 60px 0;
}

/* Buttons */

.button {
  border-radius: 4px;
  position: relative;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.button > span {
    width: 100%;
}
.button_icon {
  /* margin: 0 20px; */
}
.button_large_blue {
  font-size: 1rem;
  width: 200px;
  height: 50px;
  background: #005ff8;
  padding: 0 15px;
  margin: 0 auto;
}

/* Call section */

.calls_container {

    width: 100%;
    height: 100%;
    background: #E5E5E5;

  }
.calls__wrapper {
    position: relative;
    max-width: 1440px;
    margin: 0 auto;
}
  .calls__days_option {
    display: flex;
    color: #005FF8;
    cursor: pointer;
    font-size: 0.8rem;
    align-items: center;
    position: relative;
    gap: 0 8px;
  }
  .calls_calendar {
    position: absolute;
    top: 70px;
    right: 0;
  }
  .calls__days_leftarrow {
    display: inline-block;
    background: url('../assets/icons/arrow_left.png') no-repeat;
    height: 10px;
    width: 10px;
  }

  .calls__days_rightarrow {
    display: inline-block;
    background: url('../assets/icons/arrow_right.png') no-repeat;
    height: 10px;
    width: 10px;
  }
  .calls__days_calendar {
    display: inline-block;
    background: url('../assets/icons/calendar.png') no-repeat;
    height: 18px;
    width: 16px;
  }
  .calls__upside_options {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 20px 0;
    gap: 0 48px;
  }
  .calls__balance {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0 10px;
    font-size: 0.8rem;
    max-width: 145px;
    width: 100%;
    height: 40px;
    background: #FFF;
    border-radius: 50px;
    color: #899CB1;
  }
  .calls__balance_add {
    display: inline-block;
    background: url('../assets/icons/balance_add.png') no-repeat;
    height: 24px;
    width: 24px;
  }
  .calls__input_searchfield {
    padding: 0 0 0 25px;
    background: url('../assets/icons/search.png') no-repeat;
  }
  .calls__input_searchfield:focus {
    padding: 0 0 0 25px;
    background: url('../assets/icons/search_blue.png') no-repeat;

  }
  .calls__select_calendar {
    -webkit-appearance: none;
  -moz-appearance: none;
width: 45px;
  }
  .calls__select_calendar::-ms-expand {
    display: none;
  }
.calls__dropdowns_options {
    display: flex;
    gap: 0 15px;
}
.calls__dropdowns_options > select {
    width: 200px;
    font-size: 0.8rem;
    color: #899CB1;
}
.calls__dropdowns_options > select > option {
    color: #899CB1;
}
.calls__downside_options {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
}
.calls__table {
    width: 100%;
    background: #FFF;
    padding: 26px 40px;
    border-collapse: collapse; 
    border-radius: 8px;
}
.table_item,.table__head  {
    font-size: 0.8rem;
    text-align: center;
    height: 65px;
    border-bottom: 1px #EAF0FA solid
}

